import './fifthDiv.css';
import { useState } from 'react';

export default function FifthDiv() {
  let [ areFilled, setAreFilled ] = useState(true);
  let [ nom, setNom ] = useState('');
  let [ email, setEmail ] = useState('');
  let [ phoneNum, setPhoneNum ] = useState('');
  let [ msg, setMsg ] = useState('');
  function submit() {
    if(nom && email && phoneNum && msg){
      //submit Data
    }else{
      setAreFilled(false);
      setTimeout(()=> setAreFilled(true), 3000);
    }
  }
  return (
    <div className='fifthDiv'>
        <div className='textDiv'>
          <div>
            <h2>Digitechium</h2>
            <p>Le Lorem Ipsum est simplement du faux texte employé dans la composition et la mise en page avant
              impression. Le Lorem Ipsum est le faux texte standard de l'imprimerie depuis les années 1500, quand
              un imprimeur anonyme assembla ensemble des morceaux de texte pour réaliser un livre spécimen de polices
              de texte. Il n'a pas fait que survivre cinq siècles, mais s'est aussi</p>
          </div>
          <div>
            <iframe title='digitechium localisation' className='map' src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3396.4405697171755!2d-8.015270685513535!3d31.649164247938987!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x0%3A0x409959ac5e99225e!2zMzHCsDM4JzU3LjAiTiA4wrAwMCc0Ny4xIlc!5e0!3m2!1sfr!2sma!4v1645807977845!5m2!1sfr!2sma" style={{border: 0}} allowFullScreen={false} loading="lazy"></iframe>
          </div>
        </div>
        <div className="l6 w3-padding-large">
          <h2>Contactez-nous</h2>
          {areFilled ? '' : <div className='alert alert-danger'>Vous devez remplir tous les informations !</div>}
            <input type="text" className="txt-input" onChange={(e)=> setNom(e.target.value)} placeholder="Votre Nom*" />
            <input type="text" className="txt-input" onChange={(e)=> setEmail(e.target.value)} placeholder="Email Address*" />
            <input type="text" className="txt-input" onChange={(e)=> setPhoneNum(e.target.value)} placeholder="Numéro de Téléphone*" />
            <textarea type="text" className="txt-input1" onChange={(e)=> setMsg(e.target.value)} placeholder="Votre Message*" ></textarea>

            <div className="w3-center">
                <button onClick={submit} className="btn btn-info sendBtn">Envoyer</button>
            </div>
        </div>
    </div>
  )
}
