import { lazy, Suspense } from 'react';
import Loading from '../loading/loading';
import './sixthDiv.css';
import TitleAndText from '../titleAndText/titleAndText';
let ImageRow = lazy(()=> import('./cards/ImageRow'));

export default function SixthDiv() {

  return (
      <div>
        <TitleAndText title={'Parties intéressées'} text={``}  />
        <div className='sixthDiv blackBg'>
          <Suspense fallback={<Loading />}>
            <ImageRow />
          </Suspense>
        </div>
      </div>
  )
}
