import Button from './button/button';
import './filterDiv.css';

let arr = [['websites', 'website'], ['Branding', 'BRD'],
['réseaux sociaux', 'RS'], ['Events', 'events'], ['Autres', 'autre']]
export default function FilterDiv({ handleFiltration }) {
  return (
    <div className='filterDiv'>
        {
            arr.map((e, i)=>{
                return <Button key={i} handleFilter={handleFiltration} ele={e} />
            })
        }
    </div>
  )
}
