import TitleAndText from '../titleAndText/titleAndText';
import Loading from '../loading/loading';
import websites from '../testos/websites';
import FilterDiv from './filterDiv/filterDiv';
import { useState, lazy, Suspense } from 'react';
import './thirdDiv.css';
let Card = lazy(()=> import('./card/card'));
let Img = lazy(()=> import('./imageHolder/img'));
let Autre = lazy(()=> import('./autres/autres'));
let Branding = lazy(()=> import('./brandingPdf/brandingPdf'));

export default function ThirdDiv() {
  let [ items, setItems ] = useState([...websites]);
  let [ drendo, setDrendo ] = useState(false);


  function filterBy(item) {
    let newItems = websites.filter((e)=> e.type === item);
    setItems(newItems);
    setDrendo(true);
  }
  
  return (
    <div>
        <TitleAndText title={'QUELQUE REALISATIONS'} text={"brève liste des réalisations que nous avons atteint"} />
      <div className='thirdDiv container container-fluid blackBg'>
          <FilterDiv handleFiltration={filterBy} />
          <div className='websitesList'>
            <Suspense fallback={<Loading />} >
                {
                    items.map((e, i)=>{
                      if(drendo){
                        if(e.type === 'website') return <Card key={i} title={e.title} imageSrc={e.imageSrc}
                        description={e.description} link={e.link} />

                        if(e.type === 'RS') return <Img key={i} title={e.title} imageSrc={e.imageSrc}
                          link={e.link} />

                        if(e.type === 'BRD') return <Branding key={i} title={e.title} imageSrc={e.imageSrc}
                        description={e.description} link={e.link} />


                        if(e.type === 'autre') return <Autre key={i} imageSrc={e.imageSrc} />

                        
                        if(e.type === 'events') return <div imageSrc={e.imageSrc} key={i}></div>

                      }else{

                        if(e.type === 'website') return <Card key={i} title={e.title} imageSrc={e.imageSrc}
                        description={e.description} link={e.link} />
                      }
                      return '';
                    })
                }
              </Suspense>
          </div>
      </div>
    </div>
  )
}
